.category-ua {
  float: none;
  display: block;
  margin-bottom: 40px;
}

.content-wrapper {
  flex: 1;
}

@media screen and (min-width: 768px) {
  .left-side {
    display: flex;
    align-items: flex-start;
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .category-ua {
    float: left;
  }

  .left-side {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .support-wrapper {
    padding-left: 0px;
  }

  .content-wrapper {
    display: flex;
  }
}
